			<svg class="icon" aria-hidden="true" @click="openGift(3)"><use xlink:href="#icon-liwu-"></use></svg>
		</div>
      </div>
      <div class="star star-1 ts-3d" :style="stopeCss"></div>
      <div class="star star-2 ts-3d" :style="stopeCss"></div>
      <div class="star star-3 ts-3d" :style="stopeCss"></div>
      <div class="star star-4 ts-3d" :style="stopeCss"></div>
      <div class="star star-5 ts-3d" :style="stopeCss"></div>
      <div class="shine ts-3d" :style="stopeCss"></div>
    </div>
    <div class="snow-container">
      <div class="snow snow-1 snow-y-1"></div>
      <div class="snow snow-2 snow-y-2"></div>
      <div class="snow snow-3 snow-y-3"></div>
      <div class="snow snow-4 snow-y-3"></div>
      <div class="snow snow-5 snow-y-2"></div>
      <div class="snow snow-6 snow-y-1"></div>
      <div class="snow snow-7 snow-y-1"></div>
      <div class="snow snow-8 snow-y-2"></div>
      <div class="snow snow-9 snow-y-3"></div>
      <div class="snow snow-10 snow-y-3"></div>
    </div>


	<el-dialog  custom-class="gift" title="圣诞礼物" :visible.sync="showGift" width="80%" :center="true">
		<img v-if="giftIndex==1" src="../img/jiezi.jpg">
		<img v-if="giftIndex==2" src="../img/bao.jpg">
		<img v-if="giftIndex==3" src="../img/caomei.jpg">
	</el-dialog>

	<el-dialog  custom-class="gift" title="圣诞音乐" :visible.sync="showMusic" width="80%" :center="true" :destroy-on-close="true" :append-to-body="true" @close="close" @opened="open">
		<audio id="audio" preload="none" controls>
			<source v-if="musicIndex==1" src="../img/music1.mp3" type="audio/mpeg">
			<source v-if="musicIndex==2" src="../img/music2.mp3" type="audio/mpeg">
			<source v-if="musicIndex==3" src="../img/music3.mp3" type="audio/mpeg">
		</audio>
	</el-dialog>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
		isStop:false,//是否停止旋转

		showGift:false,//展示礼物
		giftIndex:1,
		showMusic:false,//展示音乐
		musicIndex:1,

		isFly:false,
    };
  },

  computed:{
	  stopeCss(){
		  if(this.isStop){
			  return {"animation-play-state":"paused","-webkit-animation-play-state":"paused"}
		  }
		  return {"animation-play-state":"running","-webkit-animation-play-state":"running"}
	  },
  },

  mounted(){

  },

  methods:{
	  start(){
		  this.isStop = !this.isStop;
	  },

	  mouseenter(){
		  console.log(123);
		  this.isStop = true;
	  },
	  mouseleave(){
		  console.log(345);
		this.isStop = false;
	  },
		
	 openGift(index=1){
		 this.giftIndex = index;
		 this.$nextTick(()=>{
			 this.showGift = true;
		 }); 
	 },		
	openMusic(index=1){
		this.musicIndex = index;
		this.$nextTick(()=>{
			this.showMusic = true;
		});
		
		
	},
	open(){
		document.getElementById("audio").play();
	},
	close(done){
		document.getElementById("audio").pause();
	},

	fly(){
		this.isFly = true;
		setTimeout(()=>{
			this.isFly = false;
		},1000);
	}
  }
};
</script>

