.star,
.star:before,
.star:after {
	content: '';
	height: 0;
	width: 0;
	border-top: solid 30px var(--star-bg);
	border-left: solid 45px transparent;
	border-right: solid 45px transparent;
	position: absolute;
	top: 0;
	left: calc(50% - 45px);
}

.star:before {
	transform: rotate(72deg);
	top: -33px;
	left: -46px;
}

.star:after {
	transform: rotate(287deg);
	top: -33px;
	left: -44px;
}

.shine {
	background: var(--shine-bg);
