	width: 90px;
	position: absolute;
	top: -45px;
	left: calc(50% - 45px);
	border-radius: 50%;
	transform: translateZ(40px);
	filter: blur(20px);
	opacity: 0.6;
	animation: 5s shine linear infinite;
}

@keyframes shine {
